.fullpage {
  width: 100%;
  height: calc(100vh-120px);

  transition: all 1s ease-in-out;
}
.circleItem {
  height: 200px;
}
.t {
  transition: all 1s ease-in-out;
}
.blue {
  background-color: #97f;
}

.signin {
  /* padding: 16px; */
  background-color: red;
}
.textcenter {
  margin: 0 auto;
  text-align: center;
  padding: 32px;
}

.bcshowleft {
  transform: translateX(-100px);
  /* height: 20%; */
}

.bcshowright {
  transform: translateX(100px);
  /* height: 20%; */
}
.bcshowno {
  transform: translateX(0px);
  /* height: 100%; */
}

.textBackDrop {
  background-color: rgba(220, 220, 220, 0.95);
  padding: 12px;
  margin: 6px;
  border: none;
  border-radius: 12px;
}
.dropTitle {
  text-align: center;
  color: rgb(30, 30, 30) !important;

  min-height: 80px;
}
.dropSubTitle {
  text-align: justify;
  color: rgb(30, 30, 30) !important;

  min-height: 160px;
  line-height: 2em;
  font-size: 1.4em;
  letter-spacing: 0.01em;
}

@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
  .textBackDrop {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(130, 110, 110, 0.15);
  }
}

.imageFit {
  object-fit: cover;
  font-weight: 900;
}
